<template>
  <div class="sc-theme easyui-panel" :style="getTableStyle()">
    <table :id="tableId" :height="heights">
      <thead frozen="true">
      <tr>
        <th field="region" width="200" align="left">Region</th>
      </tr>
      </thead>
      <thead>
        <tr id="thead">
          <th v-for="item in columns" :width="getThStyle(item)" :field="item.fieldName" align="center" :editor="item.editor">{{item.title}}</th>
        </tr>
      </thead>
    </table>
  </div>
</template>
<style scoped>

</style>
<script>
  export default {
    name: 'TreeGrid',
    props:{
      data:{
          type:Array,
          default(){
              return [];
          }
      },
      column:{
        type:Array,
        default(){
          return [];
        }
      },
      handleBtn:{
        type:Array,
        default(){
          return [];
        }
      },
      url:{
        type:String,
        default(){
          return "";
        }
      },
      width:{
        type:String,
        default(){
          return "100%";
        }
      },
      height:{
        type:String,
        default(){
          return "";
        }
      },
      fitColumn:{
          type:Boolean,
          default(){
              return true
          }
      },
      rowNumbers:{
        type:Boolean,
        default(){
          return true
        }
      },
      collapseAll:{
        type:Boolean,
        default(){
          return true
        }
      },
      treeParams:{
        type:Object,
        default(){
          return []
        }
      },
    },
    data () {
        var vIdField = "";
        var vTreeField = "";
        var vParentId = "";
        if(this.treeParams){
          vIdField = this.treeParams.idField;
          vTreeField = this.treeParams.treeField;
          vParentId = this.treeParams.parentId
        }
      return {
        requestUrl:this.url,
        columns:this.column,
        rownumbers:this.rowNumbers,
        idField:vIdField,
        treeField:vTreeField,
        fitColumns:this.fitColumn,
        treeCollapseAll:this.collapseAll,
        tableData:this.data,
        fit:this.fit,
        heights:this.height,
        controlList:this.handleBtn,
        tableStyle:{
          width:this.width,
          height:"600"
        },
        editingId:undefined,
        tableId:""
      }
    },
    watch:{
      url:{
          handler(val){
              this.requestUrl = val;
          },
          deep:true
      },
      tableData:{
        handler:function(val){
          if(val.length>0){
            this.tableData = val;
             //this.loadTreeGrid();
          }
        },
        deep:true
      },
      height:{
          handler(val){
              this.heights = val;
          },
          deep:true
      }
    },
    mounted() {
      window.vue = this;
      this.loadTreeGrid();
    },
    methods:{
      getTableStyle(){
          return ";width:"+this.width+"!important;height:"+(this.heights+2)+"!important";
      },
      refshTreeGrid(){  //
        $("#"+this.tableId).datagrid('reload');
      },
      getThStyle(item){
        if(item.width){
          return item.width
        }else{
          return 60;
        }
      },
      loadTreeGrid(){
        var _this = this;
        window.controlList = _this.controlList;
        $("#"+this.tableId).treegrid({
          url:_this.requestUrl,
          method:"get",
          fitColumns:_this.fitColumn,
          fit:_this.fit,
          rownumbers: _this.rowNumbers,
          showFooter: false,
          animate:false,
          idField:_this.idField,
          striped:true, //
          treeField:_this.treeField,
          onLoadSuccess:function(row,data){  //
            if(!_this.treeCollapseAll){
              $("#"+_this.tableId).treegrid('collapseAll');
            }
            $.each(data.rows,function(index,val){
              $(".tree-icon").attr("style","background:none!important"); //
              //$(".tree-hit.tree-expanded").attr("style","background:none!important");  // 
              // $(".tree-hit").eq(index).attr("class",val.iconNodeClass);
            });
            window.handleClick = _this.handleClick;
            $(".tree-title").on("click",function(){
              $(this).parent().find(".tree-hit").trigger("click");
            });
            $(".datagrid-header-row td[field='fn']").width($(".datagrid-row td[field='fn']").width());
            $(".datagrid-row").each(function(index){
              var vhtml = "";
              for(var i=0;i<controlList.length;i++){
                vhtml+= "<span class='"+controlList[i].iconClass+"' style='color:"+controlList[i].color+"' name='"+controlList[i].name+"' data='"+JSON.stringify(data.rows[index])+"' onclick=\"treeCtrClickEvent(event,undefined)\"></span>";
              }
                $(this).find("td[field='fn']>div").append(vhtml);
            });
            _this.computeDataGrid();
          },
          onClickCell:function(rowIndex, field, value){
              if(rowIndex=="fn"){
                treeCtrClickEvent(undefined,field);
              }
          },
          onExpand:function(row){ // row
            _this.$emit("on-expand",row);
          },
          onCollapse:function(row){ //
            _this.$emit("on-collapse",row);
          },
        });
        // $("#table").datagrid("loadData",_this.tableData);
      },
      computeDataGrid(){
        var gridBodyInner = $(".datagrid-body-inner>table tbody tr");
        var gridBody = $(".datagrid-body>table tbody tr");
        var num = 0;
        for(var i=0;i<gridBodyInner.length;i++){
            var theadHeight =  $("#"+$(gridBodyInner[i]).attr("id")).height();
            var tbodyTdHeight = $("#"+$(gridBody[i]).attr("id")).height();
            var MaxHeight = theadHeight>tbodyTdHeight?theadHeight:tbodyTdHeight;
            num+=MaxHeight;
            var css=function(t,s){
              s=document.createElement('style');
              s.innerText=t;
              document.body.appendChild(s);
            };
           css("#"+$(gridBodyInner[i]).attr("id")+"{height:"+MaxHeight+"px!important}");
           css("#"+$(gridBody[i]).attr("id")+"{height:"+MaxHeight+"px!important}");
        }
        console.log(num);
      },
      getStyle(item){
        try{
          if(item.width){
            return item.width;
          }else{
            return "auto";
          }
        }catch(e){console.log(e)}
      },
     isIE() { //ie?
        if (!!window.ActiveXObject || "ActiveXObject" in window) {
           this.fit = true;
        }else{
           this.fit = false;
        }
       }
    },
    beforeMount() {
      this.isIE();
      this.tableId = 'tableId' + Math.floor(Math.random()*1000)
    },

    updated(){
    }
  }
  $.extend($.fn.datagrid.methods, {   //
    editCell: function(jq,param){
      return jq.each(function(){
        var opts = $(this).datagrid('options');
        var fields = $(this).datagrid('getColumnFields',true).concat($(this).datagrid('getColumnFields'));
        for(var i=0; i<fields.length; i++){
          var col = $(this).datagrid('getColumnOption', fields[i]);
          col.editor1 = col.editor;
          if (fields[i] != param.field){
            col.editor = null;
          }
        }
        $(this).datagrid('beginEdit', param.index);
        for(var i=0; i<fields.length; i++){
          var col = $(this).datagrid('getColumnOption', fields[i]);
          col.editor = col.editor1;
        }
      });
    }
  });
  window.formatOper = function(val,row,index){  //val,row,,index
    var vhtml = "";
    for(var i=0;i<controlList.length;i++){
      vhtml+= "<span class='"+controlList[i].iconClass+"' style='color:"+controlList[i].color+"' name='"+controlList[i].name+"' data='"+JSON.stringify(row)+"' onclick=\"ctrClickEvent(event)\"></span>";
    }
    return vhtml;
  };
  var iconName = "";
  window.treeCtrClickEvent = function(e,rowData){
      if(e){
        iconName = $(e.target).attr("name");
      }
      if(rowData){
        vue.$emit("handle-click",iconName,rowData)
      }
  }
</script>



